import React from 'react'

const PropertyItem = ({data}) => {
    const {buildingName, floor, facing, address, amount, cabin, conference, workStation, isAC, isPantry, isWashroom, isFreeHold } = data;

    const renderDetail = (value, name) => {
      return (
        <div className="col s4 m2 center">
          {/* <div className="black-text">{value}</div> {name} */}
          <h6 className="black-text">{value}</h6>
          <div className="grey-text">{name}</div>
        </div>
      );
    };

    const getFloorSuffix = (i) => {
        let j = i % 10,
            k = i % 100;
        if (j === 1 && k !== 11) {
            return i + "st floor";
        }
        if (j === 2 && k !== 12) {
            return i + "nd floor";
        }
        if (j === 3 && k !== 13) {
            return i + "rd floor";
        }
        return i + "th floor";
    }

    const renderAmenities = (value, trueName, falseName) => {
      return (
        <div className="col s6 m3 center">
          {value 
            ? (<span className="green-text"><i class="material-icons">check</i>{trueName}</span>)
            : (<span className="red-text"><i class="material-icons">close</i>{falseName}</span>)}
        </div>
      );
    };

    return (
    //   <div class="col s12">
    //   <div class="card">
    //     <div class="card-content">
    //       <span class="card-title">{`${buildingName} in ${address}`}</span>
    //       <h6 className="green-text">Rs {amount}</h6>
    //       <div className="row">
    //         {renderDetail(getFloorSuffix(floor), "Floor")}
    //         {renderDetail(facing, "Facing")}
    //         {renderDetail(cabin, "Cabin")}
    //         {renderDetail(conference, "Conference")}
    //         {renderDetail(workStation, "Work station")}
    //         {renderDetail(`Rs ${amount}`, "Amount")}
    //         {renderDetail(`Rs ${amount}`, "Maintenance")}
    //         {renderDetail("12312333", "Area")}
    //       </div>
    //       <div className="row">
    //         {renderAmenities(isAC, "A/C", "Non-A/C")}
    //         {renderAmenities(isPantry, "Pantry", "No Pantry")}
    //         {renderAmenities(isWashroom, "Washroom", "No Washroom")}
    //         {renderAmenities(isFreeHold, "Free Hold", "No Free-Hold")}
    //       </div>
    //     </div>
    //     {/* <div class="card-action green">
    //         <h6 className="white-text">Rs {amount}</h6>
    //         <a href="#">Add to Favorite</a>
    //         <a href="#">View Details</a>
    //     </div> */}
    //   </div>
    // </div>
    <div class="card">
    <div class="card-content">
      <span class="card-title activator grey-text text-darken-4">{buildingName}<i class="material-icons right">more_vert</i></span>
      <h6 class="grey-text">{`${getFloorSuffix(floor)} / ${facing}`}<span className="right green-text">{`Rs ${amount}`}</span></h6>
      <div className="row">
        {renderDetail(cabin, "Cabin")}
        {renderDetail(conference, "Conference")}
        {renderDetail(workStation, "Work station")}
        {renderDetail(`Rs ${amount}`, "Maintenance")}
        {renderDetail("8,993 sq.ft.", "Super Area")}
        {renderDetail("8,000 sq.ft.", "Built-up Area")}
      </div>
             <div className="row">
             {renderAmenities(isAC, "A/C", "Non-A/C")}
             {renderAmenities(isPantry, "Pantry", "No Pantry")}
             {renderAmenities(isWashroom, "Washroom", "No Washroom")}
             {renderAmenities(isFreeHold, "Free Hold", "No Free-Hold")}
           </div>
    </div>
    <div class="card-reveal">
      <span class="card-title grey-text text-darken-4"> TITLE <i class="material-icons right">close</i></span>
      <p>N/A</p>
    </div>
  </div>
    )
}

export default PropertyItem

